import React from 'react';
import { Row, Col, Card, Table, OverlayTrigger, Tooltip } from 'react-bootstrap';
import Loader from '../../App/layout/Loader/index'
import Aux from "../../hoc/_Aux";
import { getItemCatagories, deleteItemCategory } from '../../api/inventory/itemCategory';
import IsEmpty from '../../App/components/Reusables/IsEmpty';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import DeleteModal from '../../App/components/Reusables/DeleteModal';
import isEmpty from '../../validation/isEmpty';
import Spinner from '../../App/components/Reusables/Spinner';
import Paging from '../../App/components/Reusables/Paging';
import { getStartEnd } from '../../helpers/getStartEnd';

// import { getVendors } from '../../api/inventory/vendorApi';

const Items = (props) => {

    const [loading, setLoading] = React.useState(true)
    const [itemCategoriesArr, setItemCategoriesArr] = React.useState([]);
    const [deleteData, setDeleteData] = React.useState({ showModal: false, itemCategoryId: null, itemsLength: null })
    const [instanceCount, setInstanceCount] = React.useState(0)


    // const match = React.useMatch()

    React.useEffect(() => {

        const controller = new AbortController();
        getItemCatagories({
            'action': 'get',
            'filter': 'none',
            ...getStartEnd(props)
        }, controller.signal)
            .then(async res => {
                console.log(res);
                if (res.json.status) {
                    setItemCategoriesArr(res.json.item_catagories);
                    setInstanceCount(res.json.count)
                    await setLoading(false)
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            })
            .catch(err => console.log(err))
        return () => controller.abort();
    }, []);


    const _triggerDeleteModal = (cat) => {
        setDeleteData({ showModal: true, itemCategoryId: cat.id, itemsLength: cat.count_item });
    }


    const _delete = (toBeDeletedId) => {
        deleteItemCategory({ action: 'delete', item_categories_id: [toBeDeletedId] })
            .then(async (res) => {
                console.log(res)
                if (res.json.status) {
                    const deletedItem = itemCategoriesArr.find(item => item.id === toBeDeletedId);
                    let tempArr = itemCategoriesArr;
                    tempArr.splice(tempArr.indexOf(deletedItem), 1);
                    await setItemCategoriesArr([...tempArr])
                    setDeleteData({ showModal: false, itemCategoryId: null })
                    Jhilikka.fire({ icon: 'success', title: 'Item Category deleted successfully!', background: '#dffff0' })
                    // props.history.push('/item-categories')
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err);
            });
    }


    const ItemCategoriesList = itemCategoriesArr.map((item, index) => {
        return (
            <tr key={index}>
                <th scope="row">{item.id}</th>
                <td
                    onClick={() => {
                        props.history.push('/item-category/' + item.id)
                    }}
                    style={{ cursor: 'pointer', color: '#55588b' }}>{item.name}</td>
                {/* <td>{item.name}</td> */}
                <td>{item.count_item}</td>
                <td>{item.parent_str}</td>
                <td>{item.is_active ? 'Yes' : 'No'}</td>
                <td>
                    <i
                        className="feather icon-edit text-c-green f-19 m-r-10"
                        style={{ cursor: 'pointer' }}
                        onClick={() => props.history.push('/item-category/update/' + item.id)}
                    />
                    <i
                        className="feather icon-trash-2 text-c-red f-19 m-r-5"
                        style={{ cursor: 'pointer' }}
                        onClick={() => _triggerDeleteModal(item)}
                    />
                </td>
            </tr>
        )
    })

    console.log(deleteData)

    return (
        <Aux>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10" style={{ lineHeight: '33px' }}>Item Categories</h4>
                <div>
                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={() => props.history.push(`item-category/create`)}
                    >
                        <i className="feather icon-plus text-c-white f-15 m-r-5" />
                     Add New
                    </button>
                </div>
            </Row>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            <Table striped responsive>
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Name</th>
                                        <th>No. of Items</th>
                                        <th>Parent</th>
                                        <th>Enabled</th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {ItemCategoriesList || ''}
                                </tbody>
                            </Table>
                            {isEmpty(itemCategoriesArr) && !loading ? <><IsEmpty>Data not available</IsEmpty><br /><br /></> : ''}
                            {loading ? <Spinner /> : null}
                            <Paging instanceCount={instanceCount} />
                        </Card.Body>

                    </Card >

                </Col>
            </Row>
            <DeleteModal
                show={deleteData.showModal}
                onCancel={() => setDeleteData({ ...deleteData, showModal: false, itemsLength: null })}
                onDelete={() => _delete(deleteData.itemCategoryId)}
                message={
                    deleteData.itemsLength > 0 ?
                        'First, delete the items associated to this category to delete this cateogry.' :
                        null
                }
                noRedButton={deleteData.itemsLength > 0}
            />
        </Aux>
    );
}

export default Items;